<template>
  <div class="login-container">
    <img class="head-image" src=""/>
    <div class="register-tag-view btn-text">
      新用户注册
    </div>
    <div class="input-view">
      <login-input cla @changeValue="passwordChange" place-holder="请设置6-12位密码" right-text="获取验证码"></login-input>
    </div>
    <div class="input-view">
      <login-input @changeValue="surePasswordChange" place-holder="请确认密码"></login-input>
    </div>
    <div class="next-btn" @click="loginHandle">确认并登陆</div>
    <div class="back-btn btn-text" @click="lastStep">返回上一步</div>
    <div class="foot-view btn-text" @click="backLogin">返回登录</div>
  </div>
</template>

<script>

  import loginInput from '../../components/common/loginInput';
  import { navigateTo, navgationBack } from '../../utils/mRouter';

  export default {
    name: 'register_password',
    components: { loginInput },
    methods: {
      passwordChange(value) {
        console.log(value);
      },
      surePasswordChange(value) {
        console.log(value);
      },
      loginHandle() {
        // todo 验证登陆
        navigateTo('../index/index', 'index', '', this);
      },
      lastStep() {
        navgationBack(-1, this);
      },
      backLogin() {
        navgationBack(-2, this);
      },
    },
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .login-container {
    .flx-y;
  }
  .head-image {
    .wh(2,2);
    .borderRadius(1);
    .margin-top(0.24);
    background-color: gold;
  }
  .register-tag-view {
    .margin-top(0.32);
    .text-color1;
    .fontSize-two;
  }
  .btn-text {
    .text-color3;
    .fontSize-five;
  }
  .next-btn {
    .wh(6.06,0.88);
    .borderRadius(0.08);
    .flx-x-center;
    .margin-top(0.72);
    .fontSize-three;
    background-color: #45D4FB;
    color: white;
  }
  .back-btn {
    .wh(6.06,0.88);
    .line-height(0.88);
  }
  .foot-view {
    position: absolute;
    .height(0.88);
    .line-height(0.88);
    bottom: 20px;
  }
  .input-view {
    .wh(7.5,1.4);
    .flx-y-end;
  }

</style>



